.highlight {
  padding: 14px;
  margin-bottom: 24px;
  background-color: #f7f7f9;
  border: 1px solid #e1e1e8;
  border-radius: 4px;
  text-align:left;
  line-height:1.2;
  font-size:12px;
  // max-width:640px;
  margin-right:auto;
  margin-left:auto;
}
.highlight pre {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  // white-space: nowrap;
  background-color: transparent;
  border: 0;
}
.highlight pre code {
  font-size: inherit;
  color: #333;
  /* Effectively the base text color */
}
.highlight pre .lineno {
  display: inline-block;
  width: 22px;
  padding-right: 5px;
  margin-right: 10px;
  color: #bebec5;
  text-align: right;
}
